.about-project {
    display: flex;
    width: 100%;
    max-width: 1280px;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    margin: 0;
    padding: 110px 70px 25px 70px;
    box-sizing: border-box;
}
.about-project__project {
    font-weight: 400;
    font-size: 22px;
    line-height: 1.2;
    color: #000000;
    border: none;
    letter-spacing: -1px;
    margin: 0 0 25px 0;
}
.about-project__info {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-top: 2px solid #000000;
    box-sizing: border-box;
    padding: 70px 0 0 0;
    margin: 0 0 110px 0;
}

.about-project__about {
    margin: 0 0 0 50px;
}
.about-project__about:first-child {
    margin: 0;
}
.about-project__title {
    font-weight: 400;
    font-size: 20px;
    line-height: 1.2;
    color: #000000;
    letter-spacing: -0.7px;
    margin: 0 0 26px 0;
}
.about-project__text {
    font-weight: 400;
    font-size: 14px;
    line-height: 1.43;
    color: #000000;
    margin: 0;
}

.about-project__deadline {
    display: grid;
    width: 100%;
    align-items: center;
    grid-template-columns: 228px 1fr;
    grid-template-rows: 36px 36px;
    margin: 0 0 80px 0;
}
.about-project__one-week {
    height: 36px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 400;
    font-size: 14px;
    line-height: 1.2;
    text-align: center;
    color: #FFFFFF;
    background: #3456F3;
    margin: 0;
}
.about-project__four-week {
    display: flex;
    height: 36px;
    justify-content: center;
    align-items: center;
    font-weight: 400;
    font-size: 14px;
    line-height: 1.20;
    color: #000000;
    background-color: #F2F2F2;
    text-align: center;
    margin: 0;
}
.about-project__backend {
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 500;
    font-size: 14px;
    line-height: 1.20;
    text-align: center;
    color: #A0A0A0;
    margin: 10px 0 0 0;
}
.about-project__front-end {
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 500;
    font-size: 14px;
    line-height: 1.20;
    text-align: center;
    color: #A0A0A0;
    margin: 10px 0 0 0;
}

@media screen and (max-width: 769px) {
    .about-project {
        margin: 0;
        padding: 92px 50px 25px 50px;
    }
    .about-project__project {
        line-height: 1.1;
        margin: 0 0 22px 0;
        /*border: 1px solid #000000;*/
    }
    .about-project__info {
        margin: 0 0 94px 0;
    }
    .about-project__about {
        margin: 0 0 0 5.9%;
    }
    .about-project__about:first-child {
        margin: 0;
    }
    .about-project__title {
        letter-spacing: -0.8px;
        margin: 0 0 23px 0;
    }
    .about-project__text {
        font-size: 12px;
        line-height: 1.45;
    }
    .about-project__deadline {
        grid-template-columns: 140px 1fr;
        margin: 0 0 60px 0;
    }
    .about-project__four-week {
        width: 100%;
    }
}

@media screen and (max-width: 450px) {
    .about-project {
        display: flex;
        width: 100%;
        padding: 72px 18px 68px;
    }
    .about-project__deadline {
        grid-template-columns: 100px 1fr;
        grid-template-rows: 29px 29px;
        margin: 0;
    }
    .about-project__project {
        font-size: 18px;
        letter-spacing: -0.04em;
        margin: 0 0 28px 0;
    }
    .about-project__info {
        flex-direction: column;
        margin: 0 0 61px 0;
        padding: 62px 0 0 0;
        border-top: 1px solid #000000;
    }
    .about-project__about:first-child {
        margin: 0 0 56px 0;
    }
    .about-project__about {
        margin: 0 0 0 0;
    }
    .about-project__title {
        font-size: 18px;
        letter-spacing: -0.04em;
        margin: 0 0 17px;
    }
    .about-project__text {
        font-size: 11px;
    }
    .about-project__one-week {
        font-size: 11px;
        max-width: 100%;
    }
    .about-project__four-week {
        font-size: 11px;
        max-width: 100%;
    }
    .about-project__backend {
        font-size: 11px;
        max-width: 100%;
    }
    .about-project__front-end {
        font-size: 11px;
        max-width: 100%;
    }
}